import React, { useEffect } from 'react'
import {
    Routes,
    Route,
    useLocation
} from "react-router-dom";
import { CSSTransition, TransitionGroup } from 'react-transition-group'

import ContactMePage from '../pages/ContactMePage/ContactMePage';
import FourZeroFour from '../pages/errorsPages/404/404';
import MainPage from '../pages/MainPage/MainPage';

const routes = [
    { path: "/", Component: MainPage },
    { path: "main", Component: MainPage },
    { path: "contact-me", Component: ContactMePage },
    { path: "*", Component: FourZeroFour },
]


function AppRouter() {
    const location = useLocation();

    useEffect(() => {
        window.scrollTo(0, 0);
    }, [location]);


    return (
        <TransitionGroup>
            <CSSTransition
                key={location.key}
                timeout={300}
                classNames="page"
                unmountOnExit={true}
            >
                <Routes>
                    {routes.map(route =>
                        <Route
                            path={route.path}
                            element={
                                <div className='page'>
                                    <route.Component />
                                </div>
                            } key={route.path}
                        />
                    )}
                </Routes>
            </CSSTransition>
        </TransitionGroup>
    )
}

export default AppRouter;