import { useState } from 'react'
import BasicLayout from "./components/BasicLayout/BasicLayout";
import AppRouter from "./routerSettings/AppRouter";
import { HashRouter } from "react-router-dom";
import { ThemeContext, themes } from "./context/themes";

import "./styles/main.scss";


function App() {
  const getTheme = () => {
    let theme_from_local_storage = localStorage.getItem("theme")
    if (theme_from_local_storage) {
      let t = themes[theme_from_local_storage]
      if (t) {
        return t;
      } else {
        console.error("Wrong theme was in the localStorage")
        return themes.light
      }
    } else {
      return themes.light
    }
  }

  const [theme, setTheme] = useState(getTheme())

  const changeTheme = (th) => {
    let theme_to_save = Object.keys(themes).find(key => themes[key] === th)
    if (theme_to_save) {
      setTheme(th)
      localStorage.setItem("theme", theme_to_save)
    } else {
      console.error("This theme does not exist")
    }
  }

  return (
    <HashRouter hashType="slash">
      <ThemeContext.Provider value={theme}>
        <BasicLayout changeTheme={changeTheme}>
          <AppRouter />
        </BasicLayout>
      </ThemeContext.Provider>
    </HashRouter>
  );
}

export default App;
