import React, { useEffect, useState, useContext } from 'react';
import { useTranslation } from 'react-i18next';
import cookies from 'js-cookie';

import M from "materialize-css/dist/js/materialize.min.js";
import "materialize-css/dist/css/materialize.min.css";
import SidebarMenu from '../SidebarMenu/SidebarMenu';

import cl from "./Navbar.module.scss";
import { ThemeContext } from '../../context/themes';
import { themes } from '../../context/themes';
import { languages } from '../../languages/languages';

import ThemeSvg from './svg-icons/ThemeSvg';
import LangSvg from './svg-icons/LangSvg';

const SelectLang = function ({ chLang, currentLanguageCode, languages, color }) {
    return (
        <label className={cl.label}>
            <span className={cl.svg}>
                <LangSvg color={color} />
            </span>
            <select defaultValue={currentLanguageCode} onChange={chLang} className={`${cl.select}`}>
                {languages.map(({ code, name, country_code }) =>
                    <option key={country_code} value={code} className={cl.option}>
                        &nbsp;{name}&nbsp;
                    </option>
                )}
            </select>
        </label>
    )
}

const SelectTheme = ({ chTheme, currentTheme, themes, color }) =>
    <label className={cl.label}>
        <span className={cl.svg}>
            <ThemeSvg color={color} />
        </span>
        <select defaultValue={currentTheme} onChange={chTheme} className={cl.select}>
            {Object.keys(themes).map((theme) =>
                <option key={theme} value={theme} className={cl.option}>
                    &nbsp;{theme}&nbsp;
                </option>
            )}
        </select>
    </label>

const Burger = ({ color }) =>
    <span className={cl.burger_wrapper_main}>
        <span data-target="slide-out" className={`sidenav-trigger ${cl.burger_wrapper}`}>
            <span className={cl.burger}>
                <svg fill={color} viewBox="0 0 46 32" width="26" height="20">
                    <path
                        d="M0 0h45.714v4.571h-45.714v-4.571zm0 13.714h45.714v4.571h-45.714v-4.571zm0 13.715h45.714v4.571h-45.714v-4.571z">
                    </path>
                </svg>
            </span>
        </span>
    </span>




function Navbar({ changeTheme }) {
    const currentLanguageCode = cookies.get('i18next') || 'en'
    const currentLanguage = languages.find((l) =>
        l.code === currentLanguageCode
    )
    const { t, i18n } = useTranslation()

    useEffect(() => {
        document.body.dir = currentLanguage.dir || 'ltr'
    }, [currentLanguage, t])

    const chLang = (e) => {
        e.preventDefault()
        console.log(e.target.value)
        i18n.changeLanguage(e.target.value)
    }


    useEffect(() => {
        let sidenav = document.querySelector("#slide-out");
        M.Sidenav.init(sidenav, {})
    }, [])


    const theme = useContext(ThemeContext);

    const [currentTheme, setCurrentTheme] = useState(
        Object.keys(themes).filter((key) => {
            return themes[key] === theme
        })[0]
    )

    const chTheme = (e) => {
        e.preventDefault()
        changeTheme(themes[e.target.value])
        console.log(e.target.value)
        setCurrentTheme(e.target.value)
    }

    return (
        <React.Fragment>
            <nav className={cl.nav_main_wrapper} style={{
                background: theme.nav.background,
                boxShadow: `0 5px 20px 2px ${theme.nav.boxShadow}`,
            }}>
                <SelectLang chLang={chLang} currentLanguageCode={currentLanguageCode} languages={languages} color={theme.nav.iconColor} />
                <SelectTheme chTheme={chTheme} currentTheme={currentTheme} themes={themes} color={theme.nav.iconColor} />
                <Burger color={theme.nav.burgerColor} />
            </nav>
            <SidebarMenu />
        </React.Fragment >
    )
}

export default Navbar;