import React, {useEffect} from 'react';
import { useTranslation } from 'react-i18next';
import cl from './MainPage.module.scss';
import axios from "axios";
import { basic_url, headers } from '../../API/APISettings';


const mainPageView = async () => {
    try {
        axios.get(`${basic_url}/blog/main_page_view/`, {
        headers: headers,
        params: {}
    }) } catch {
        return null
    }
}


function MainPage() {
    const { t } = useTranslation()

    useEffect(()=>{
        mainPageView()
    }, [])

    return (
        <>
            <h2>{t('pages.main-page.title',)}</h2>
            <br />
            <p>
                {t('pages.main-page.content')}
            </p>
        </>
    );
}

export default MainPage;
