import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { NavLink } from 'react-router-dom';
import cl from "./SidebarMenu.module.scss";

import { ThemeContext } from '../../context/themes';

function SidebarMenu() {
    const closeSidebar = () => {
        try {
            document.getElementsByClassName('sidenav-overlay')[0].click()
        } catch {
            try {
                document.getElementsByClassName('sidenav-overlay')[1].click()
            } catch {
                try {
                    document.getElementsByClassName('sidenav-overlay')[2].click()
                } catch {
                    console.error("Menu wasn't closed.")
                }
            }
        }
    }

    const theme = useContext(ThemeContext);

    const { t } = useTranslation()

    return (
        <div id="slide-out" className={`sidenav ${cl.sidebarMenu_mainWrapper}`} style={{
            background: theme.nav.background,
            color:"yellow",
        }}>
            <br />
            <NavLink to="/" onClick={closeSidebar}>
                {t('main-content.menu.nav.main-page')}
            </NavLink>
            <br />
            <br />
            <NavLink to="contact-me" onClick={closeSidebar}>
                {t('main-content.menu.nav.contact-me')}
            </NavLink>
        </div>
    );
}

export default SidebarMenu;
